<template>
	<div class="farm-message" ref="farmMessage">
		<!--  -->
		<div class="seed">: {{ seed }}</div>
		<!--  -->
		<div class="fertilizer">: {{ fertilizer }}</div>
		<!--  -->
		<div class="depot">: {{ depot }}</div>
		<!--  -->
		<div class="earnings">: {{ earnings }}</div>
		<!--  -->
    <!-- <div class="directPush">: {{ directPush }}</div> -->
    <div class="directPush" v-if="isAgent === 0" @click="apply"></div>
    <div class="directPush" v-if="isAgent === 1"></div>
    <div class="directPush" v-if="isAgent === 2" @click="detail">{{ directPush }}</div>
    <!--  -->
    <notice-modal></notice-modal>
  </div>
</template>
<script>
  import NoticeModal from '@/components/modal/notice-modal'
  export default {
    name: 'farm-message',
    props: {
      seed: {
        type: Number,
        default: 0
      },
      fertilizer: {
        type: Number,
        default: 0
      },
      depot: {
        type: Number,
        default: 0
      },
      earnings: {
        type: Number,
        default: 0
      },
      directPush: {
        type: Number,
        default: 0
      },
      isAgent: {
       type: Number,
       default: 0
      }
    },
    data () {
      return {
      }
    },
    mounted () {
      this.setHeight()
    },
    components: {
      NoticeModal
    },
    methods: {
        setHeight () {
          let messageElement = $(this.$refs.farmMessage).children()
          let width = $(messageElement[0]).width()
          let eachHeight = this.getHeight(width)
          $.each(messageElement, function(index, val) {
           val.style.lineHeight = val.style.height = eachHeight + 'px'
          });
        },
       getHeight (width) {
        return width * (65 / 240)
       },
       apply() {
        Bus.$emit('openConfirmModal');
       },
       detail() {
        Bus.$emit('openAgentDetailModal');
       }
    }
  }
</script>
<style scoped lang="less" type="text/less">
  .farm-message {
   flex: 1;
   display: flex;
   margin-top: 2%;
   font-size: 0.8rem;
   color: #fff;
   position: relative;
   * {
    flex: 1;
    margin: 0 0.3rem;
    background-image: url('~@/assets/status-bg.png');
    background-size: 100% 100%;
    background-repeat: no-repeat;
    text-align: center;
    white-space: nowrap;
  }
  .noticeModal {
    background-image:none;
  }
}
</style>



